class Project {
    constructor(project, title, description, tips, keys){
        this.project = project;
        this.title = title;
        this.description = description;
        this.tips = {
            tips: tips,
        }
        Object.keys(keys).forEach( key => this.tips[key] = keys[key]);
    }
}

